<template>
  <div>
    <cms-breadcrumb to="/dictionaryType" @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt :collapse='false'>
        <li>设置为系统字典，表示该字典重要程度较高</li>
      </cms-prompt>
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px">
        <el-form-item label="字典名称" prop="dictName" :error='errorMessage.dictName'>
          <el-input v-model="formData.dictName" :autofocus="true" ref="first"></el-input>
        </el-form-item>
        <el-form-item label="字典类型" prop="dictType" :error='errorMessage.dictType'>
          <el-input v-model="formData.dictType"></el-input>
        </el-form-item>

        <el-form-item label="是否系统内置" prop="isSystem" :error='errorMessage.isSystem'>
          <cms-switch v-model="formData.isSystem"></cms-switch>
        </el-form-item>
        <el-form-item label="备注信息" prop="remark" :error='errorMessage.remark'>
          <el-input v-model="formData.remark" type="textarea" :autosize="{ minRows: 3}"></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="save(false)" v-color>保存并新增</cms-button>
        <cms-button @click="save(true)" v-color>新增</cms-button>
        <cms-button type='rest' @click="rest">重置</cms-button>
        <!-- <cms-button type='rest' @click="test">mock测试</cms-button> -->
      </div>
    </div>
  </div>
</template>

<script>
import rules from '@/rules/index.js'
import form from '@/mixins/form.js'
import { dictTypeManagerSave, checkDictType } from '@/api/setting/dictionary.js'
var defaultData = {
  dictName: '',
  dictType: '',
  isSystem: true,
  remark: '',
  sortNum: 10
}
export default {
  name: 'dictionaryTypeSave',
  mixins: [form],
  data() {
    var isDictType = (rule, value, callback) => { // 验证类型是否重复
      checkDictType({ dictType: value }).then(res => {
        if (res.data) {
          callback()
        } else {
          callback(new Error('字典类型已存在'))
        }
      })
    }
    return {
      formData: Object.assign({}, defaultData),
      rules: {
        dictName: [rules.required('请填写字典名称'), rules.len(1, 24)],
        dictType: [
          rules.required('请填写字典类型'),
          rules.len(1, 150),
          rules.english(),
          rules.custom(isDictType)
        ],
        isSystem: [rules.required()],
        remark: [rules.max(140, '最大长度不能超过140个字符')]
      }
    }
  },
  methods: {
    save(state) {
      this.saveForm(dictTypeManagerSave, this.formData, state, '/dictionaryType', 'list', {})
    }
  },
  created() {
    this.formData = Object.assign({}, defaultData)
  }
}
</script>

<style>
</style>



